import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import { Redirect } from "react-router-dom";

import FixedAssetRevaluationUpdateForm from "./updateForm";
import History from "./history";


import notification from "../../../../components/notification";
import { Spin, Tooltip } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/asset/fixedAssetRevaluation/actions";
import { Box } from "../../../App/app.style";
import { Tabs } from "antd";
import Print from "./print";

const TabPane = Tabs.TabPane;
const redirectTo = "/dashboard/fixed-asset-revaluations";


class UpdateFixedAssetRevaluation extends Component {

  state = {
    visible: false,
  }
  // Get All data diawal
  componentDidMount() {
    const { editFixedAssetRevaluation } = this.props;
    const { id } = this.props.match.params;
    editFixedAssetRevaluation(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.fixedAssetRevaluation.error) {
      notification("error", this.props.fixedAssetRevaluation.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.fixedAssetRevaluation.success) {
      notification("success", this.props.fixedAssetRevaluation.success);
      this.props.clearSuccess();
    }
  }

  handleSubmit = value => {
    const { updateFixedAssetRevaluation } = this.props;
    const { id } = this.props.match.params;

    updateFixedAssetRevaluation(id, value);
  };

  handleSubmitCancel = value => {
    const { cancelFixedAssetRevaluation } = this.props;
    const { id } = this.props.match.params;

    cancelFixedAssetRevaluation(id, value);
  }

  handleModalHistory = () => {
    this.setState({ visible: !this.state.visible })
  };

  render() {
    // console.log(this.props);
    const { fixed_asset_revaluation, loading } = this.props.fixedAssetRevaluation
    const data = fixed_asset_revaluation ? fixed_asset_revaluation : []
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    return (
      <LayoutWrapper>  
        <Box>
          <PageHeader title={<IntlMessages id="fixedAssetRevaluations.update.title" />}
            style={{ margin:0 }}
          >
             <Tooltip placement="top" title="Info">
              <Button
                style={{
                  fontSize: "24px",
                  border: 0
                  // color: theme.palette.warning
                }}
                icon="issues-close"
                onClick={() => this.handleModalHistory()}
                loading={loading}
              >
                {/* <IntlMessages id="itemRequests.history" /> */}
              </Button>
            </Tooltip>

            <Print loading={loading} data={fixed_asset_revaluation} />

            <Button 
              style={{ marginLeft: "5px" }} 
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <History
            visible={this.state.visible}
            handleModalHistory={this.handleModalHistory}
            description={fixed_asset_revaluation ? fixed_asset_revaluation.description : null}
          />
          
          
            {this.props.fixedAssetRevaluation.loadingEdit ? (
              <div className="spinStyle">
                <Spin size="large" />
              </div>
            ) : (
              <FixedAssetRevaluationUpdateForm
                submit={this.handleSubmit}
                fixedAssetRevaluation={this.props.fixedAssetRevaluation}
                permissions={permissions}
                processFixedAssetRevaluation={this.props.processFixedAssetRevaluation}
                handleSubmitCancel={this.handleSubmitCancel}
                getItems={this.props.getItems}
              />    
            )}
          
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { 
    fixedAssetRevaluation: state.fixedAssetRevaluation, 
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateFixedAssetRevaluation);
